.about {
  padding: 1px 10px 0 10px;
  width: 100%;
  position: relative;
  max-width: 1280px;
  margin: 0 auto;

  @media screen and (max-width: 320px) {
    padding: 1px 5px 0 5px;
  }
}

.about__wrapper {
  background-color: $white;
  border-radius: 8px;
  padding-top: 20px;
  width: 100%;
  margin-top: -10px;
  z-index: 2;


  @media screen and (max-width: 1024px) {
    padding-top: 10px;
  }

  @media screen and (max-width: 320px) {
    padding-top: 20px;
  }
}

.about__text {
  font-family: "Inter", Arial, Helvetica, sans-serif;
  font-size: 16px;
  color: $black;
  line-height: 22px;
  padding-top: 40px;
  margin: 0 auto 0 50px;
  max-width: 760px;

  @media screen and (max-width: 1024px) {
    font-size: 15px;
    max-width: 680px;
  }

  @media screen and (max-width: 768px) {
    max-width: 648px;
  }

  @media screen and (max-width: 320px) {
    max-width: 270px;
    padding-top: 30px;
    margin: 0 auto 0 20px;
    font-size: 12px;
    line-height: 16px;
  }
  @media screen and (max-width: 768px) {
    max-width: 648px;
    margin: 0 50px;
  }
  @media screen and (max-width: 480px) {
    margin: 0 20px;
  }
  @media screen and (max-width: 320px) {
    max-width: 270px;
    padding-top: 30px;
    margin: 0 20px;
    font-size: 12px;
    line-height: 16px;
  }
}

.about__header {
  font-family: "Inter", Arial, Helvetica, sans-serif;
  font-size: 50px;
  color: $black;
  line-height: 60px;
  font-weight: 400;
  font-style: italic;
  margin: 90px auto 0 50px;

  @media screen and (max-width: 1024px) {
    margin-top: 80px;
    font-size: 46px;
    line-height: 56px;
  }

  @media screen and (max-width: 768px) {
    font-size: 42px;
    line-height: 51px;
  }
  @media screen and (max-width: 480px) {
    margin: 60px auto 0 20px;
  }
  @media screen and (max-width: 320px) {
    font-size: 32px;
    line-height: 38px;
    margin: 60px auto 0 20px;
    max-width: 270px;
  }
}

.about__list-element {
  list-style-type: disc;
  margin-bottom: 14px;
  list-style-position: inside;
}